// hs text colors based on hs-accent-colors and hs-theme-grays 

@each $color,
$value in $hs-accent-colors {
    .hs-text-#{$color} {
        color : $value;
    }
}

@each $color,
$value in $hs-theme-grays {
    .hs-text-gray-#{$color} {
        color : $value;
    }
}

// override in variables.scss 
$hs-large-font-size:2.2em !default;
$hs-large-font-weight:300 !default;
$hs-small-font-size:0.75em !default;
$hs-small-font-weight:400 !default;

// font helpers for consistency 
.hs-font-large {
    font-size:$hs-large-font-size !important;
    font-weight:$hs-large-font-weight;
}
.hs-font-small {
    font-size:$hs-small-font-size !important;
    font-weight:$hs-small-font-weight;
}

// remove outline on text refiner on focus 
input[hsrole="refiner"] {
    outline-width:0; 
}

@mixin hs-line-clamp($lines) {
    line-height: 1rem;
    height:calc(1rem * #{$lines});
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: $lines;
    -webkit-box-orient: vertical;
  }
  
  @for $i from 1 through 10 {
      .hs-line-clamp-#{$i} {
          @include hs-line-clamp(#{$i});
      }
  }