// define css variables for default teams mode
:root {
    --hst-font-family: "Segoe UI", system-ui, "Apple Color Emoji", "Segoe UI Emoji", sans-serif;
    --hst-brand: #464775;
    --hst-text-primary: #242424;
    --hst-text-secondary: #424242;
    --hst-text-tertiary: #616161;
    --hst-background-primary: #f5f5f5;
    --hst-background-component: #ffffff;
    --hst-background-component-alt: #ebebeb;
    --hst-background-hover: #f5f5f5;
    --hst-grid-row-color: #242424;
    --hst-grid-header-color: #242424;
    --hst-background-header: #FFF;
    --hst-border-color: #f5f5f5;
    --hst-tab-default: #616161;
    --hst-tab-active: #242424;
    --hst-tab-border: var(--hst-border-color);
    --hst-tab-border-active: var(--hst-brand);

}

// override these variables when MS Teams is in 'dark' mode. 
body.hsbody.hsteams.v-dark,
body.hsbody.hsteams.v-contrast {
    // --hst-font-family: same in all themes
    --hst-brand: #9EA2FF;
    --hst-background-primary: #1f1f1f;
    --hst-text-primary: #ffffff;
    --hst-text-secondary: #f5f5f5;
    --hst-text-tertiary: #bdbdbd;
    --hst-background-component: #3B3A39;
    --hst-background-component-alt: #424242;
    --hst-background-hover: #1f1f1f;
    --hst-grid-row-color: #D6D6D6;
    --hst-grid-header-color: #ffffff;
    --hst-background-header: #3B3A39;
    --hst-border-color: #1f1f1f;
    --hst-tab-active: #242424;
    --hst-tab-border: var(--hst-border-color);
    --hst-tab-border-active: var(--hst-brand);
}

// this needs a complete re-write, see _msteams.scss.txt for the original code that is just a f'ing mess. 


.hsbody.hsteams {
    font-family: var(--hst-font-family);
    background-color: var(--hst-background-primary);
    color: var(--hst-text-primary);

    .text-primary {
        color: var(--hst-text-primary) !important;
    }

    .text-secondary {
        color: var(--hst-text-secondary) !important;
    }

    .text-tertiary {
        color: var(--hst-text-tertiary) !important;
    }


    // kendo card section
    .k-card {
        background-color: var(--hst-background-component);
        color: var(--hst-text-primary);

        .k-card-header {
            background-color: var(--hst-background-component);
            color: var(--hst-text-primary);

            .k-card-subtitle {
                color: var(--hst-text-secondary);
            }
        }
    }

    // kendo listview and bootstrap list-group and table 
    .k-listview {

        &,
        .k-listview-content {
            background-color: var(--hst-background-component);
            color: var(--hst-text-primary);
        }
    }

    .list-group {
        .list-group-item {
            background-color: var(--hst-background-component);
            color: var(--hst-text-primary);
            border-color: var(--hst-border-color);

            &:hover {
                background-color: var(--hst-background-hover);
            }
        }
    }

    table.table {

        &,
        td {
            background-color: var(--hst-background-component);
            color: var(--hst-text-primary);
            border-color: var(--hst-border-color);
        }
    }

    //Kendo Tabs Section
    .k-tabstrip-wrapper {
        min-height: 1.8rem;
        background-color: var(--hst-background-primary);

        .k-content.k-state-active {
            background-color: var(--hst-background-primary);
            border: none;
        }

        .k-tabstrip-items {
            border: none;
            margin-bottom: 0;

            .k-item {
                &.k-first {
                    margin-left: 0;
                }

                &.k-last {
                    margin-right: 0;
                }

                font-weight: 400;
                font-size: 14px;
                height: 2.6rem;
                padding: 0;
                margin:0 6px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                border: 0;
                border-radius: 0;
                max-width: 20rem;

                .k-link {
                    color: var(--hst-text-primary);
                    padding-left: 0 !important;
                    padding-right: 0 !important;
                }

                &.k-state-active {
                    .k-link {
                        color: var(--hst-text-secondary);
                        font-weight: 600;
                    }

                    background-color:var(--hst-background-primary);
                    border-bottom:3px solid var(--hst-tab-border-active);

                    &:hover {
                        border-bottom: 3px solid var(--hst-tab-border-active);
                    }
                }

                &:hover {
                    border-bottom: 3px solid var(--hst-tab-border);
                }
            }
        }
    }
}